<template>
	<div class="Sku_select">
		<header><i class="iconfont icon-xiangzuo1" @click="toReceipt"></i><span>绑定商品条码-选择商品</span></header>
		<div class="container">
			<table cellspacing="0">
				<thead>
					<tr>
						<td>选择</td>
						<td @click="SortArr('SkuBound_SelectSkuData','sku','SortCondition1',$event)">商品代码</td>
						<td @click="SortArr('SkuBound_SelectSkuData','desc','SortCondition2',$event)">商品名称</td>
						<td @click="SortArr('SkuBound_SelectSkuData','susr2','SortCondition3',$event)">单位</td>
					</tr>
				</thead>
				<tbody>
					<tr v-for='(dataItem,index) in SkuBound_SelectSkuData'  :id="index"  :key='index'  @click="selectData(index)">
						<td class="iconfont"  :class="{ 'icon-xuanzhong':index==detail_item,'icon-weixuanzhong':index!=detail_item}"></td>
						<td>{{dataItem.sku}}</td>
						<td>{{dataItem.desc}}</td>
						<td>{{dataItem.susr2}}</td>
					</tr>
				</tbody>
			</table>
			<div class="kong"></div>
			<p class="positionFixed" ><span class="g_singleBtn" @click="selectData_submit">确定</span></p>
		</div>
	</div>
</template>
<script type="text/javascript">
	export default{
		name:"Sku_select",
		data(){
			return{
				SkuBound_SelectSkuData:[],//列表数据
				detail_item:0,//默认为第一个
				SortCondition1:false,
				SortCondition2:false,
				SortCondition3:false,

			}
		},
		mounted(){
			$("thead").find("td").eq(0).siblings().append('<i class="iconfont"></i>')
		},
		created(){
			console.log(this.$store.state.SkuBound_SelectSkuData)

			if(this.$store.state.SkuBound_SelectSkuData.length){

				var SkuBound_SelectSkuData = this.$store.state.SkuBound_SelectSkuData;

				this.SkuBound_SelectSkuData = SkuBound_SelectSkuData;

			}
		},
		methods:{
			// 返回上一页 录入单号 页面
			toReceipt:function(){

				var that = this;

				this.$router.push({
					name:"SkuBound",
					query:{
						Sku:that.$route.query.Sku
					}
				})
			},
			//点击选择具体客户
			selectData:function(idx){

				this.detail_item = idx;

				
			},
			// 点击确认按钮 跳转到绑定商品条码页面
			selectData_submit:function(){

                var StorerKey=this.SkuBound_SelectSkuData[this.detail_item].StorerKey//客户代码
				var Sku = this.SkuBound_SelectSkuData[this.detail_item].sku//商品代码
				var Descr=this.SkuBound_SelectSkuData[this.detail_item].desc//商品名称
				

				this.$router.push({
					name:"SkuBound",
					query:{
						StorerKey:StorerKey,
						Sku:Sku,
						Descr:Descr
					}
				})
			}
		}
	}
</script>
<style lang="less" scoped>
	.Sku_select{
		&>.container{
			width:100%;
			padding-bottom: 130/50rem;
			table{
				tr{
					td:nth-child(2){
						min-width: 200/50rem;
					}
					td:nth-child(3){
						min-width: 380/50rem;
					}
					td:nth-child(4){
						min-width: 130/50rem;
					}
				}
			}
			.iconfont{
				font-size: 40/50rem;
			}		
		}
	}
	@media screen and (max-width: 250px){
		.Sku_select{
			&>.container{
				table{
					tr{
						td:nth-child(2){
							min-width: 300/50rem;
						}
						td:nth-child(3){
							min-width: 520/50rem;
						}
						td:nth-child(4){
							min-width: 200/50rem;
						}
					}
				}
				.iconfont{
					font-size: 50/50rem;
				}
			}
		}
	}
</style>


